@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --Pale-blue: hsl(225, 100%, 94%);
  --Bright-blue: hsl(245, 75%, 52%);
  --Very-pale-blue: hsl(225, 100%, 98%);
  --Desaturated-blue: hsl(224, 23%, 55%);
  --Dark-blue: hsl(223, 47%, 23%);
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Red Hat Display", sans-serif;
}
.App {
  background: url(/order-summary/static/media/pattern-background-desktop.5a0ab689.svg), hsl(225, 100%, 94%);
  background: url(/order-summary/static/media/pattern-background-desktop.5a0ab689.svg), var(--Pale-blue);
  background-repeat: no-repeat;
}
@media screen and (max-width: 375px) {
  .App {
    background-image: url(/order-summary/static/media/pattern-background-mobile.c8d50fcb.svg);
  }
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10% 0;
}
.card {
  border-radius: 10px;
  width: 30vw;
  display: grid;
  background: white;
}
@media screen and (max-width: 768px) {
  .card {
    width: 40vw;
  }
}
@media screen and (max-width: 425px) {
  .card {
    width: 60vw;
  }
}
@media screen and (max-width: 375px) {
  .card {
    width: 70vw;
  }
  .card-content{
    font-size: 13px !important;
    /* display: none; */
  }
}
.card-image {
  border-radius: 10px 10px 0px 0px;
  width: 100%;
  object-fit: cover;
}
.txt-center {
  text-align: center;
}
.order-details {
  border-radius: 10px;
  background-color: var(--Very-pale-blue);
  margin: 10px 20px;
  display: grid;
  grid-template-columns: 20% 50% 30%;
  align-items: center;
  padding: 10px 0px;
}

.icon-music {
  justify-self: center;
  width: 80%;
}
.proceed-button {
  text-decoration: none;
  background-color: var(--Bright-blue);
  margin: 10px 10px 0 20px;
  padding: 10px;
  border-radius: 10px;
  color: white;
}
.proceed-button:hover {
  background-color: var(--Desaturated-blue);
}

.cancel-button {
  text-decoration: none;
  margin: 10px 10px 20px 20px;
  color: rgba(0, 0, 0, 0.5);
  padding: 10px;
}
.cancel-button:hover {
  color: black;
}
.card-title {
  margin-top: 25px;
  font-size: 1.1rem;
}
.card-content {
  padding: 20px;
  font-size: 16px;
  color: rgba(0, 0, 0, 0.5);
}
.order-change:hover {
  text-decoration: none;
}
.order-change {
  color: var(--Bright-blue);
  font-size: 0.7rem;
}
.plan-details {
  font-size: 0.7rem;
}
.plan-title {
  font-weight: 800;
}

